.ns-form {
  margin: 20px 0 30px;
}

.ns-select {
  margin: 10px;
}

.ns-select .divider.text {
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.ui.celled.table tr td:first-child, .ui.celled.table tr th:first-child {
  border-left: 1px solid rgba(34,36,38,0.1);
}
